.goodUser {
	display: block;
	clear: both;
}
.goodUser .userTitle {
	padding: 0rem 0.5rem;
	line-height: 2rem;
	border-bottom: solid 1px #EEEEEE;
	font-size: 0.7rem
}
.goodUser .userTitle small {
	color: #666666;
}
.goodUser .userTitle .Umore {
	color: #666666;
	float: right;
}
.goodUser .userTitle .Umore span {
	color: orangered;
	margin-right: 0.2rem;
}
.goodUser .userTitle .Umore i {
	font-size: 0.7rem;
	line-height: 2rem;
	margin-left: 0.3rem;
}
.userTalk {
	padding: 0.5rem ;
	background: white;
	margin: 0.5rem 0rem;
}
.userTalk .userMeg {
	width: 100%;
	display: inline-block;
}
.userTalk .userMeg .userMeg-img {
	width: 2.3rem;
	height: 2.3rem;
	overflow: hidden;
	border-radius: 50%;
	float: left;
}
.userTalk .userMeg .userMeg-img img {
	display: block;
	width: 100%;
}
.userTalk .userMeg .userMeg-Nmae {
	float: left;
	margin-left: 0.5rem;
	line-height: 0.8rem;
	padding-top: 0.3rem;
}
.userTalk .userMeg .userMeg-Nmae p {
	color: #333333;
	margin: 0px;
	font-size: 0.6rem;
}
.userTalk .userMeg .userMeg-Nmae span {
	color: #666;
	font-size: 0.6rem;
}
.userTalk .userMeg .userMeg-evaluate {
	float: right;
	color: orangered;
	line-height: 2.3rem;
}
.userTalk .userMeg .userMeg-evaluate i {
	font-size:1rem;
	color: orangered;
	float: left;
}
.userTalk .userMeg .userMeg-evaluate span small {
	display: block;
	float: right;
    font-size: 0.6rem;
   
	margin-left: 0.2rem;
}
.userTalk-content{
	width: 100%;
}
.userTalk-content p{
	line-height: 1.5em;
	font-size: 0.65rem;
	color: #333333;
	margin: 0.2rem 0rem;
}
.userTalk-content .take-imgs{
	width: 100%;
	padding: 0.3rem 0rem;
}
.userTalk-content .take-imgs ul{
	display: inline-block;
	width: 100%;
}
.userTalk-content .take-imgs li img{
	/*display: block;
	width: 100%;
	height: 5.3rem;*/
}
.userTalk-content .take-imgs ul.one li{
	width: 50%;
	display: block;
	
	margin: 0px auto;
}
.userTalk-content .take-imgs ul.two li{
	width: 31.33%;
	float: left;
	margin:1% 1%;
}

/*回复*/
.userTalk-revert{
	width: 100%;
	background: #F6F6F6;
}
.userTalk-revert ul{
	padding: 0.3rem;
}
.userTalk-revert ul li{
	width: 100%;
	min-height: 1.2rem;
	line-height: 0.7rem;
     font-size: 0.6rem;
}
.userTalk-revert ul li .userImg{
	float: left;
	width: 0.8rem;
	height: 0.8rem;
	border-radius: 50%;
	overflow: hidden;
}
.userTalk-revert ul li .userImg img{
	display: block;
	width: 100%;
	height: 1rem;
}
.userTalk-revert ul li span{ 
   line-height: 1rem;
   font-size: 0.5rem;
   color: #3a3a3a;
}
.userTalk-revert ul li span.Uname{
    padding: 0rem 0.2rem;
}
.userTalk-revert ul li span.Uname.gf{
	color: orangered;
}

