.alert {
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 999;
	top: 0px;
	left: 0px;
}

.alert .tip {
	position: fixed;
	top: 30%;
	left: 50%;
	display: block;
	width: 10rem;
	padding: 1rem 0rem;
	min-height: 2rem;
	margin-left: -5rem;
	border-radius: 0.5rem;
	background-color: rgba(00, 00, 00, 0.5);
	text-align: center;
	color: white;
	font-size: 0.7rem;
}

.alert .tip p {
	margin-top: 0.3rem;
}

.alert .tip.hint i {
	font-size: 1.5rem;
}

.alert .tip.warning {
	background: white;
	color: #333333;
	-webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1), 0 3px 3px 0 rgba(0, 0, 0, 0.1);
	        box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1), 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}

.alert .tip.warning i {
	font-size: 2rem;
	color: red;
	font-weight: bold;
}

.alert .tip.true {
	background: white;
	color: #333333;
	-webkit-box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1), 0 3px 3px 0 rgba(0, 0, 0, 0.1);
	        box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1), 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}

.alert .tip.true i {
	font-size: 2rem;
	color: #009688;
	font-weight: bold;
}

