	height: 2rem;
	margin: 0px auto;
	border-radius: 50%;
	text-align: center;
	line-height: 2rem;
	color: red;
	background: white;
	display: block;
	 padding: 0.3rem;
	font-weight: bold; 
	animation: xz 20s infinite linear;
	-webkit-animation: xz 1s infinite linear;
	-moz-animation: xz 1s infinite linear;
	-o-animation: xz 1s infinite linear;
	-ms-animation: xz 1s infinite linear;
	
}

.alert .tip.warning i {
	font-size: 2rem;
	display: block;
	/*/* Rotate div */
	transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	/* Internet Explorer */
	-moz-transform: rotate(45deg);
